.container--advantage {
    @extend %container;
    padding: 104px 31px 99px 31px;

    @media (min-width: 768px) {
        padding: 100px 39px 100px 40px;
    }

    @media (min-width: 1200px) {
        padding: 120px 70px 120px 100px;

        display: flex;
        flex-direction: row-reverse;
    }
}

.advantage__paragraph {
    @extend %paragraph;
    font-size: 18px;
    line-height: 1.67;

}

.advantage__number,
.advantage__description {
    font-family: OpenSans, sans-serif;
    line-height: 1.36;
    text-align: center;
    letter-spacing: 0.05em;
    color: $headingsColor;
}

.advantage__number {
    display: inline-block;
    font-weight: 700;
    font-size: 42px;

    margin: 0;
}

.advantage__description {
    display: block;

    font-weight: 600;
    font-size: 11px;

    margin: 6px 0 0;
}

.advantage__list {
    @extend %list;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    flex-wrap: wrap; 

    @media (min-width: 480px) {
        flex-direction: row;
        min-width: 450px;
    }
    
    @media(min-width: 768px) {
        margin-left: 239px;
    }

    @media (min-width: 1200px) {
        margin-left: 0;
        width: 370px;
        margin-right: 50px;
        
    }
}

.plus {
    display: inline-block;

    font-family: OpenSans, sans-serif;
    font-weight: 600;
    font-size: 16px;
    line-height: 1.375;
    color: $btnBorderAndAccent;

    margin: 0;
}

.advantage__item {
    @extend %item;
    background-color: $itemsBgColor;

   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;

   padding-top: 20px;
   padding-bottom: 20px;
   width: 194px;
   height: 133px;

   &:nth-child(2) {
    @media (min-width: 480px) {
        margin-top: 35px;
    }
    
   }

   &:nth-child(3) {
    @media (min-width: 480px) {
        margin-top: -35px;
    }
    
   }

   @media (min-width: 768px) {
    width: 210px;
   }

   @media (min-width: 1200px) {
    width: 170px;
   }

  

   &--content {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: center;
    flex-wrap: wrap;

    max-width: 122px;
   }
}

.pretitle--advantage {
    @extend %pretitle;
     
    &::before {
        top: 111px;

        @media (min-width: 768px) {
            top: 107px;
            left: 40px;
        }

        @media (min-width: 1200px) {
            top: 127px;
            left: 600px;
        }
    }
}

.advantage__content {
    @media(min-width: 768px) {
        max-width: 500px;
    }
}