.footer__input::placeholder,
.footer__textarea::placeholder {
    font-family: OpenSans, sans-serif;
    font-weight: 600;
    font-size: 11px;
    line-height: 1.4;
    letter-spacing: 0.1em;
    color: $textOnBgImage;
} 

.footer__input:not(:placeholder-shown),
.footer__textarea:not(:placeholder-shown) {
    color: $primaryBackgroundColor;
}

.footer__input:hover,
.footer__input:focus,
.footer__textarea:hover,
.footer__textarea:focus {
    
    &::placeholder {
        color: $primaryBackgroundColor;
    }
}

.footer__input,
.footer__textarea {
    display: block;

    border-bottom: 1px solid $textOnBgImage;
    border-top: none;
    border-right: none;
    border-left: none;

    margin-top: 44px;
    width: 100%;
    padding: 0 0 8px 0;
    background-color: transparent;

    &:hover,
    &:focus {
        border-bottom: 1px solid $primaryBackgroundColor;
    }
}

.footer__input {
    @media (min-width: 768px) {
        width: 48%;
    }
}

.contacts__link { 
    @extend %link;
    display: flex;
    align-items: center;
    gap: 16px;

    font-family: OpenSans, sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.67;
    letter-spacing: 0.02em;
    color: $textOnBgImage;
    text-decoration: none;

    &--map {
        font-weight: 700;
    }
}

.hours {
    @extend %pretitle;
    font-family: OpenSans, sans-serif;
    font-weight: 600;
    font-size: 11px;
    line-height: 1.4;
    color: $primaryBackgroundColor;
    text-transform: uppercase;

    &::before {
        position: absolute;
        top: 5px;
        background-color: $primaryBackgroundColor;
        left: 0;
    }
}

.contacts__container--low {
    position: relative;
    margin: 44px 0 0 0;

    @media (min-width: 768px) {
        margin: 0 0 0 30px;
        display: inline-block;
        top: -100px;
        right: -30px;
    }

    @media (min-width: 1200px) {
        display: block;
        top: 70px;
        left: 0;
        margin: 0;
    }
}

.contacts__paragraph {
    font-size: 18px;
    line-height: 1.67;
    color: $textOnBgImage;
}

.footer__form {
    text-align: center;
    width: 100%;

    @media (min-width: 768px) {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
}

.booking {
    padding: 84px 31px;
    background-color: $darkBackground;
    max-width: 480px;
    margin: 0 auto;

    @media (min-width: 768px) {
        padding: 100px 39px;
        min-width: 768px;
    }

    @media (min-width: 1200px) {
        margin: 0;
        padding: 130px 10px 130px 215px;
        min-width: 50%;
    }
}

.button--booking {
    @extend %button;

    @media (min-width: 768px) {
        margin-left: 0;
    }
}

.contacts {
    padding: 84px 31px;
    background-color: $darkBackground;
    //max-width: 480px;
    min-width: 100%;
    margin: 0 auto;

    background-image: linear-gradient(to right, rgba(25, 28, 38, 1), 
    rgba(25, 28, 38, 0.393)), 
    url(../images/contacts/mobile/contacts.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

    @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
            background-image: linear-gradient(to right, rgba(25, 28, 38, 1),
             rgba(25, 28, 38, 0.393)), 
             url(../images/contacts/mobile/contacts@2x.jpg);
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center;

            @media (min-width: 768px) {
                background-image: linear-gradient(to right, rgba(25, 28, 38, 1),
                            rgba(25, 28, 38, 0.393)),
                        url(../images/contacts/tablet/contacts-tab@2x.jpg);
                    background-repeat: no-repeat;
                    background-size: cover;
                    background-position: center;
            }

            @media (min-width: 768px) {
                background-image: linear-gradient(to right, rgba(25, 28, 38, 1),
                            rgba(25, 28, 38, 0.393)),
                            url(../images/contacts/desktop/contacts-desk@2x.jpg);
                background-repeat: no-repeat;
                background-size: cover;
                background-position: center;
            }
        }

    &__adress {
        font-style: normal;
        display: inline-block;

        @media (min-width: 768px) {
            max-width: 270px;
        }
    }

    &__item {
        @extend %item;
    }

    &__item:not(:last-child) {
        margin-bottom: 20px;
    }

    @media (min-width: 768px) {
        position: relative;
        padding: 100px 39px;
        min-width: 100%;

        background-image: linear-gradient(to right, rgba(25, 28, 38, 1),
                    rgba(25, 28, 38, 0.393)),
                url(../images/contacts/tablet/contacts-tab.jpg);
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center;
    }

    @media (min-width: 1200px) {
        padding: 130px 100px 120px 100px;
        margin: 0;
        min-width: 50%;

        background-image: linear-gradient(to right, rgba(25, 28, 38, 1),
                    rgba(25, 28, 38, 0.393)),
                url(../images/contacts/desktop/contacts-desk.jpg);
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center;
    }
}

.list--contacts {
    @extend %list;
    margin-top: 40px;
}



/* Post-footer */

.list--lowest {
    @extend %list;
    position: relative;
    top: 0;
    right: 0;
    left: 60%;
    margin: 0;
    //width: 270px;

    display: inline-flex;
    justify-content: flex-end;

    margin-bottom: 20px;

    @media (max-width: 479px) {
        left: 50%;
    }

    @media (min-width: 768px) {
        margin-bottom: 0;
        position: static;
    }
}

.lowest__item {
    @extend %item;

    &:first-child::before {
        content: '';
        display: block;
        position: absolute;
        background-color: $headingsColor;
        height: 1px;
        width: 60px;
        top: 10px;
        left: -100px;

        @media (min-width: 768px) {
            top: 66px;
            left: 65%;
        
        }

    }
}

.lowest {
    @extend %container;
    background-color: $itemsBgColor;

    padding: 44px 31px;
    max-height: 138px;
    min-width: 100vw;

    &__item:not(:last-child) {
        margin-right: 40px;
    }

    @media (min-width: 768px) {
        padding: 56px 39px;
        max-height: 127px;

        display: flex;
        flex-direction: row-reverse;
        justify-content: space-between;
    }

    @media (min-width: 1200px) {
        padding: 56px 215px;
    }
}

.copyright,
.lowest__link {
    font-family: OpenSans, sans-serif;
    font-weight: 600;
    font-size: 11px;
    line-height: 1.4;
    letter-spacing: 0.02em;
    color: $textOnBgImage;

    margin: 0;
}

.lowest__link {
    @extend %link;
    color: $headingsColor;
    text-decoration: none;
}

.footer {
    background-color: $darkBackground;

    @media (min-width: 1200px) {
        display: flex;
        justify-content: center;
        
    }
}